<template>
  <div class="intresTxt-page">
			<div class="textcontainer">
					<span class="particletext bubbles"><span class="text">首页</span></span>
			</div>

			<!-- <div class="textcontainer">
					<span class="particletext lines"><span class="text">Lines</span></span>
			</div>

			<div class="textcontainer">
					<span class="particletext confetti"><span class="text">Confetti</span></span>
			</div>

			<div class="textcontainer">
					<span class="particletext fire"><span class="text">Fire</span></span>
			</div>

			<div class="textcontainer">
					<span class="particletext sunbeams"><span class="text">Beams?</span></span>
			</div> -->
  </div>
</template>
<script>
export default {
  name: 'intrestTxt',
  props:{
    title: {
      type: String,
      default() {
        return '默认'
      }
    },
    kinds: {
      type: String,
      default() {
        return '0'
      }
    }, 
  },    
  data(){
    return{

    }
  },
  mounted(){
    const self=this;
    this.loadAsyncScript('https://www.shenyifan.top/apis/syf/staticJS/jquery-2.1.1.min.js',function(){
			self.loadAsyncScript('https://www.shenyifan.top/apis/syf/staticJS/bubbler.js',function(){

			})
    })
  },
  methods:{
    loadAsyncScript(url, callback) {
        var script = document.createElement('script');
        script.type = 'text/javascript';
        if (script.readyState) {
            // 兼容IE浏览器
            // 脚本加载完成事件
            script.onreadystatechange = function () {
                if (script.readyState === 'complete' || script.readyState === 'loaded') {
                    callback();
                }
            }
        } else {
            // Chrome, Safari, FireFox, Opera可执行
            // 脚本加载完成事件
            script.onload = function () {
                callback();
            }
        }
        script.src = url; //将src属性放在后面，保证监听函数能够起作用
        document.head.appendChild(script);
    }
  },
}
</script>
<style lang="scss">
.intresTxt-page{
	.textcontainer {
			padding:5px 0;
			text-align: center;
	}
	.particletext {
			text-align: center;
			font-size:12px;
			position: relative;
	}
	.particletext.bubbles > .particle {
			opacity: 0;
			position: absolute;
			background-color: rgba(33, 150, 243, 0.5);
			-webkit-animation: bubbles 3s ease-in infinite;
			animation: bubbles 3s ease-in infinite;
			border-radius: 100%;
	}
	.particletext.lines > .particle {
			position: absolute;
			background-color: rgba(244, 67, 54, 0.5);
			-webkit-animation: lines 3s linear infinite;
