	}
	.particletext.confetti > .particle.c2 {
			background-color: rgba(156, 39, 176, 0.5);
	}
	.particletext.fire > .particle {
			position: absolute;
			background-color: rgba(255, 193, 7, 0.5);
			border-radius: 40px;
