	}
	.particletext.fire > .particle:before {
			position: absolute;
			content: '';
			top: 60%;
			left: 40%;
			-webkit-transform: translate(-50%, -50%);
			transform: translate(-50%, -50%);
			width: 50%;
			height: 50%;
